.container{display:flex;flex-direction:column;gap:1rem;padding:1rem}.inputBox{display:flex;justify-content:space-between;align-items:center;border:1px solid var(--line);border-radius:1rem;padding:1rem;box-shadow:0 2px 8px rgba(0,0,0,.05)}.dark .inputBox{background-color:var(--dark);border-color:var(--on-surface)}.text{text-align:center}.label{font-size:.85rem;color:var(--secondary);margin-bottom:.25rem}.input{font-size:1.25rem;font-weight:600;background:rgba(0,0,0,0);border:none;outline:none}.input.light{color:var(--on-surface)}.input.dark{color:var(--white)}.currency{font-size:1.1rem;font-weight:600}.usd{color:var(--success)}.btc{color:var(--critical)}